.App {
  font-family: sans-serif;
  text-align: center;
}

.main {
  box-sizing: border-box;
  display: flex;
 flex-wrap: wrap;


padding:1rem;
margin:12px auto;
background: #fff;
}
.input {
  flex-grow: 1;

  border-radius: 15px;
  padding: 8px;

  border: 2px solid #e2e2e2;
font-size: 12px;
margin-right: 5px;

}

.btn {
  border-radius: 15px;
  border:2px solid floralwhite;
  background-color: #1a6ccc;
  color: #fff;
  padding: 6px 8px;
  margin-left: 4px;
 font-size: 16px;
}
